'use client';
import { useEffect } from 'react';
import Swiper from 'swiper';
import 'swiper/css';

export default function TestSwiper() {
  useEffect(() => {
    new Swiper('.swiper', { slidesPerView: 3, spaceBetween: 16 });
  }, []);

  return (
    <div className="swiper" style={{ width: '80%', height: 300 }}>
      <div className="swiper-wrapper">
        {[1, 2, 3, 4, 5].map((n) => (
          <div className="swiper-slide" key={n}>
            <img
              src={`https://picsum.photos/id/${n + 10}/800/400`}
              alt=""
              style={{ width: '100%', height: '100%', objectFit: 'cover' }}
            />
          </div>
        ))}
      </div>
    </div>
  );
}
